import React from 'react'
import PropTypes from 'prop-types'

import { Link } from 'gatsby'

import { FAIcon } from '@src-components/elements'

const TaxonomyListItem = props => {
	const { item, total, icon } = props
	return (
		<li>
			<h3>
				<Link
					to={item.node.fields.slug}
					className="inline-flex items-center text-xl border-gray-700 border-b font-bold group dark:border-gray-100"
				>
					<span className="group-hover:text-gray-700 dark:group-hover:text-gray-200">
						<FAIcon icon={icon} className="mr-1" />
						{item.node.frontmatter.title}
					</span>
					<span className="ml-2 px-1 rounded-sm outline-none bg-gray-500 text-sm text-white dark:bg-gray-100 dark:text-gray-900">
						{total}
					</span>
				</Link>
			</h3>
			<p className="inline-block mt-2 text-sm">
				{item.node.frontmatter.description}
			</p>
		</li>
	)
}

TaxonomyListItem.propTypes = {
	item: PropTypes.array,
	total: PropTypes.number,
	icon: PropTypes.object,
}

export default TaxonomyListItem
