import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'
import { css } from '@emotion/core'

import { faAngleLeft, faAngleRight } from '@fortawesome/free-solid-svg-icons'

import { FAIcon } from '@src-components/elements'
import { ThumbnailImage } from '@src-components/images'
import { AllPostContentQuery } from '@src-queries'

const PrevNext = props => {
	const { prevId, nextId } = props
	// 全記事データの取得
	const allPostContentData = AllPostContentQuery()
	// 前記事の取得
	const prevPostContentData = allPostContentData.find(
		postContentData => postContentData.node.id === prevId
	)
	// 次記事の取得
	const nextPostContentData = allPostContentData.find(
		postContentData => postContentData.node.id === nextId
	)

	if (!prevPostContentData && !nextPostContentData) {
		return null
	}

	return (
		<section className="flex flex-row justify-between mt-8 w-full border-gray-900 dark:border-gray-100 border-t-2 pb-4">
			<div className="inline-flex self-start justify-start w-1/2 h-full py-2">
				{prevPostContentData && (
					<Link
						to={prevPostContentData.node.fields.slug}
						className="block group relative w-4/5"
					>
						<figure>
							<div className="relative rounded-sm overflow-hidden border-gray-200 border">
								<ThumbnailImage
									filePath={prevPostContentData.node.frontmatter.thumbnail}
									alt={prevPostContentData.node.frontmatter.title}
									className="w-full h-auto"
								/>
							</div>
							<figcaption className="px-2 rounded-sm mt-1 bg-gray-900 group-hover:bg-gray-700 shadow dark:bg-gray-700 dark:group-hover:bg-gray-500">
								<span
									className="absolute z-10 bg-gray-900 text-white border-gray-900 border-2 rounded-sm text-sm font-semibold px-1 dark:bg-white dark:text-gray-900 dark:border-white"
									css={css`
										left: 0.5rem;
										top: -1.5rem;
									`}
								>
									<FAIcon icon={faAngleLeft} size="sm" className="mr-1" />
									新しい記事
								</span>
								<span className="text-sm font-semibold text-white">
									{prevPostContentData.node.frontmatter.title}
								</span>
							</figcaption>
						</figure>
					</Link>
				)}
			</div>
			<div className="inline-flex self-start justify-end w-1/2 h-full my-2">
				{nextPostContentData && (
					<Link
						to={nextPostContentData.node.fields.slug}
						className="block group relative w-4/5"
					>
						<figure>
							<div className="relative rounded-sm overflow-hidden border-gray-200 border">
								<ThumbnailImage
									filePath={nextPostContentData.node.frontmatter.thumbnail}
									alt={nextPostContentData.node.frontmatter.title}
									className="w-full h-auto"
								/>
							</div>
							<figcaption className="px-2 rounded-sm mt-1 bg-gray-900 group-hover:bg-gray-700 shadow dark:bg-gray-700 dark:group-hover:bg-gray-500">
								<span
									className="absolute z-10 bg-gray-900 text-white border-gray-900 border-2 rounded-sm text-sm font-semibold px-1 dark:bg-white dark:text-gray-900 dark:border-white"
									css={css`
										right: 0.5rem;
										top: -1.5rem;
									`}
								>
									古い記事
									<FAIcon icon={faAngleRight} size="sm" className="ml-1" />
								</span>
								<span className="text-sm font-semibold text-white">
									{nextPostContentData.node.frontmatter.title}
								</span>
							</figcaption>
						</figure>
					</Link>
				)}
			</div>
		</section>
	)
}

PrevNext.propTypes = {
	prevId: PropTypes.string,
	nextId: PropTypes.string,
}

export default PrevNext
