import React from 'react'
import { css } from '@emotion/core'
import { Link } from 'gatsby'

import { FAIcon } from '@src-components/elements'
import { navigationSetting } from '@src-settings/navigation.setting'

const GlobalNav = () => {
	return (
		<nav className="hidden md:block">
			<ul className="flex">
				{navigationSetting.globalNavItems.map(item => {
					return (
						<li key={item.label}>
							<Link
								to={item.url}
								className="inline-block py-2 px-4 text-gray-700 font-bold hover:text-gray-900 focus:text-gray-900 border-b border-white transition duration-150 ease-in-out
								dark:border-gray-900 dark:text-gray-200 dark:hover:text-gray-100 dark:focus:text-gray-100
								"
								css={css`
									&[aria-current],
									&:hover,
									&:focus {
										--border-opacity: 1;
										--text-opacity: 1;
										border-color: rgba(22, 30, 46, var(--border-opacity));
										color: rgba(22, 30, 46, var(--text-opacity));
									}
									.dark &[aria-current],
									.dark &:hover,
									.dark &:focus {
										--border-opacity: 1;
										--text-opacity: 1;
										border-color: rgba(229, 231, 235, var(--border-opacity));
										color: rgba(229, 231, 235, var(--text-opacity));
									}
								`}
							>
								<FAIcon icon={item.icon} size="lg" className="mr-2" />
								{item.label}
							</Link>
						</li>
					)
				})}
			</ul>
		</nav>
	)
}

export default GlobalNav
