import { useStaticQuery, graphql } from 'gatsby'

export const AllPostContentQuery = () => {
	const { allMdx } = useStaticQuery(
		graphql`
			query {
				allMdx(
					sort: { fields: [frontmatter___modifiedTime], order: DESC }
					filter: {
						frontmatter: { draft: { ne: true } }
						fields: { type: { eq: "post" } }
					}
				) {
					edges {
						node {
							id
							body
							frontmatter {
								title
								description
								thumbnail
								categories
								tags
								keywords
								publishTime
								modifiedTime
							}
							fields {
								slug
								name
								type
							}
						}
					}
				}
			}
		`
	)
	return allMdx.edges
}
