import React, { useContext } from 'react'
import { Link } from 'gatsby'
import { css } from '@emotion/core'

import { faBook, faBars } from '@fortawesome/free-solid-svg-icons'

import { AppContext } from '@src-components/contexts'
import { Wrapper, FAIcon, ScreenReaderText } from '@src-components/elements'
import { GlobalNav, GlobalSubNav, Logo } from '@src-components/parts'
import { AllSiteMetadataQuery } from '@src-queries'

const Header = () => {
	const siteMetadata = AllSiteMetadataQuery()

	const pageValues = useContext(AppContext)

	return (
		<header className="font-sans w-full bg-white shadow sticky print:static top-0 z-40 border-b dark:bg-gray-900 dark:border-gray-700">
			<Wrapper>
				<div className="flex flex-row justify-between">
					<h1 className="self-start my-4">
						<Link to={siteMetadata.pathPrefix} className="inline-block w-44">
							<Logo
								css={css`
									height: auto;
									width: 100%;
								`}
							/>
							<ScreenReaderText>{siteMetadata.title}</ScreenReaderText>
						</Link>
					</h1>
					<div className="flex flex-col justify-between">
						<div className="self-end">
							<GlobalSubNav />
						</div>
						<div className="self-end">
							<nav className="md:hidden">
								<ul className="flex">
									{pageValues?.tocItems && (
										<li className="mr-1">
											<button
												className="w-16 px-2 py-1 font-semibold text-xs text-gray-900 border-b border-white hover:border-gray-900 focus:border-gray-900 dark:text-gray-100 dark:border-gray-900 dark:hover:border-gray-100 dark:focus:border-gray-100"
												onClick={() => {
													pageValues?.setIsOpenTOC(true)
												}}
											>
												<FAIcon icon={faBook} size="lg" />
												<span className="block">目次</span>
											</button>
										</li>
									)}
									<li>
										<button
											className="w-16 px-2 py-1 font-semibold text-xs text-gray-900 border-b border-white hover:border-gray-900 focus:border-gray-900 dark:text-gray-100 dark:border-gray-900 dark:hover:border-gray-100 dark:focus:border-gray-100"
											onClick={() => {
												pageValues?.setIsOpenMenu(true)
											}}
										>
											<FAIcon icon={faBars} size="lg" />
											<span className="block">メニュー</span>
										</button>
									</li>
								</ul>
							</nav>
							<GlobalNav />
						</div>
					</div>
				</div>
			</Wrapper>
		</header>
	)
}

export default Header
