import { useEffect } from 'react'
import Spider from '@inc2734/spider/dist/js/spider'

const SpiderCarouselInitialization = () => {
	useEffect(() => {
		const spider = new Spider('.spider-carousel')
		document.addEventListener('componentDidMount', spider)
		return () => document.removeEventListener('componentDidMount', spider)
	}, [])

	return null
}

export default SpiderCarouselInitialization
