import { useStaticQuery, graphql } from 'gatsby'

export const AllAvatarImageQuery = () => {
	const { allFile } = useStaticQuery(
		graphql`
			query {
				allFile(
					filter: {
						sourceInstanceName: { eq: "contents-images-avatar" }
						ext: { regex: "/(png|jpg|jpeg)/" }
					}
				) {
					edges {
						node {
							relativePath
							sourceInstanceName
							childImageSharp {
								fluid(maxWidth: 384, quality: 100) {
									...GatsbyImageSharpFluid_withWebp_tracedSVG
								}
							}
						}
					}
				}
			}
		`
	)
	return allFile.edges
}
