import React from 'react'
import { Link } from 'gatsby'

const HookTabItem = props => {
	const { item } = props

	return (
		<li>
			<Link
				to={item.node.fields.slug}
				className="block p-2 bg-gray-100 dark:bg-gray-700 rounded-sm hover:bg-gray-200 dark:hover:bg-gray-500"
			>
				<h3 className="text-lg font-semibold">{item.node.frontmatter.title}</h3>
				<p className="mt-4 text-sm">{item.node.frontmatter.description}</p>
			</Link>
		</li>
	)
}

export default HookTabItem
