import React from 'react'
import PropTypes from 'prop-types'
import GatsbyImage from 'gatsby-image'

import { AllThumbnailImageQuery } from '@src-queries'

const ThumbnailImage = props => {
	const { filePath, alt, className } = props
	const allThumbnailImage = AllThumbnailImageQuery()

	const findThumbnailImageData = allThumbnailImage.find(thumbnailImage => {
		return thumbnailImage.node.relativePath.includes(filePath)
	})
	const defaultImageData = allThumbnailImage.find(thumbnailImage => {
		return thumbnailImage.node.relativePath.includes('_default.jpg')
	})

	const fluidData = findThumbnailImageData || defaultImageData

	return (
		<GatsbyImage
			fluid={fluidData.node.childImageSharp.fluid}
			alt={alt}
			className={className}
		/>
	)
}

ThumbnailImage.propTypes = {
	filePath: PropTypes.string,
	alt: PropTypes.string,
	className: PropTypes.string,
}

export default ThumbnailImage
