import React from 'react'
import { css } from '@emotion/core'
import classnames from 'classnames'

const H1 = props => {
	const { children, className, id, style } = props
	return (
		<h1
			id={id}
			className={classnames('relative text-6xl font-bold mb-8', className)}
			style={style}
		>
			{children}
		</h1>
	)
}

const H2 = props => {
	const { children, className, id, style } = props
	return (
		<h2
			id={id}
			className={classnames('relative text-4xl font-semibold mb-8', className)}
			style={style}
		>
			{children}
		</h2>
	)
}

const H3 = props => {
	const { children, className, id, style } = props
	return (
		<h3
			id={id}
			className={classnames(
				'relative text-2xl font-bold mb-8 px-4 py-2 bg-gray-100 dark:bg-gray-700 border-l-4',
				className
			)}
			css={css`
				border-color: #0fbbdd;
			`}
			style={style}
		>
			{children}
		</h3>
	)
}

const H4 = props => {
	const { children, className, id, style } = props
	return (
		<h4
			id={id}
			className={classnames(
				'relative text-xl font-bold mb-8 px-4 py-2 bg-gray-100 dark:bg-gray-700 rounded-sm',
				className
			)}
			css={css`
				&::after {
					--tw-bg-opacity: 1;
					border: 8px solid transparent;
					border-top: 8px solid rgba(244, 245, 247, var(--tw-bg-opacity));
					content: '';
					height: 0;
					left: 16px;
					position: absolute;
					top: 100%;
					width: 0;
				}
				.dark &::after {
					--tw-bg-opacity: 1;
					border-top: 8px solid rgba(55, 65, 81, var(--tw-bg-opacity));
				}
			`}
			style={style}
		>
			{children}
		</h4>
	)
}

const H5 = props => {
	const { children, className, id, style } = props
	return (
		<h5
			id={id}
			className={classnames(
				'relative text-xl font-medium mb-8 pb-2 border-b-2 border-gray-200 dark:border-gray-700',
				className
			)}
			css={css`
				&:after {
					background: #0fbbdd;
					bottom: -2px;
					content: '';
					height: 2px;
					left: 0;
					position: absolute;
					width: 10%;
				}
			`}
			style={style}
		>
			{children}
		</h5>
	)
}

const H6 = props => {
	const { children, className, id, style } = props
	return (
		<h6
			id={id}
			className={classnames('relative text-xl font-medium mb-8', className)}
			style={style}
		>
			{children}
		</h6>
	)
}

export { H1, H2, H3, H4, H5, H6 }
