import React, { useContext, useState, useEffect } from 'react'
import PropTypes from 'prop-types'

import { faSearch, faBars } from '@fortawesome/free-solid-svg-icons'

import { AppContext } from '@src-components/contexts'
import { Header, Footer } from '@src-components/foundations'
import {
	News,
	BreadcrumbList,
	SlideContents,
	SlideContentsHeader,
	SlideContentsContent,
	SearchForm,
	SearchResult,
	DrawerNav,
} from '@src-components/parts'

export const OneColumnLayout = props => {
	const { children } = props

	const pageValues = useContext(AppContext)

	useEffect(() => {
		pageValues?.isOpenMenu || pageValues?.isOpenSearch
			? document.body.classList.add('overflow-hidden')
			: document.body.classList.remove('overflow-hidden')
	}, [pageValues?.isOpenMenu, pageValues?.isOpenSearch])

	const [searchValue, setSearchValue] = useState('')
	const [searchResultData, setSearchResultData] = useState([])

	return (
		<div className="bg-white dark:bg-gray-900 text-gray-900 dark:text-gray-100 flex flex-col min-h-screen tracking-wide leading-8">
			<Header />
			<News />
			<BreadcrumbList />
			<main className="flex-grow pt-8 pb-12">
				<div className="container mx-auto px-4 sm:flex sm:flex-row sm:flex-nowrap">
					<article className="w-full break-all">{children}</article>
				</div>
			</main>
			<Footer />
			<SlideContents isOpen={pageValues?.isOpenMenu}>
				<SlideContentsHeader
					title="メニュー"
					icon={faBars}
					close={pageValues?.setIsOpenMenu}
				></SlideContentsHeader>
				<SlideContentsContent>
					<DrawerNav />
				</SlideContentsContent>
			</SlideContents>
			<SlideContents isOpen={pageValues?.isOpenSearch}>
				<SlideContentsHeader
					title="記事検索"
					icon={faSearch}
					close={pageValues?.setIsOpenSearch}
				>
					<SearchForm
						value={searchValue}
						onChange={event => setSearchValue(event.target.value)}
					/>
				</SlideContentsHeader>
				<SlideContentsContent>
					<SearchResult
						value={searchValue}
						searchResultData={searchResultData}
						setSearchResultData={setSearchResultData}
					/>
				</SlideContentsContent>
			</SlideContents>
		</div>
	)
}

OneColumnLayout.propTypes = {
	children: PropTypes.node.isRequired,
}
