import React, { useEffect, useState, useContext } from 'react'
import PropTypes from 'prop-types'
import { Link } from 'react-scroll'
import classnames from 'classnames'

import { AppContext } from '@src-components/contexts'

function getIds(items) {
	return items.reduce((acc, item) => {
		if (item.url) {
			acc.push(item.url.slice(1))
		}
		if (item.items) {
			acc.push(...getIds(item.items))
		}
		return acc
	}, [])
}

function useActiveId(itemIds) {
	const [activeId, setActiveId] = useState(null)
	useEffect(() => {
		const observer = new IntersectionObserver(
			entries => {
				entries.forEach(entry => {
					if (entry.isIntersecting) {
						const tocElements = document.querySelectorAll('.table-of-contents')
						for (const tocElement of tocElements) {
							tocElement.dataset.active = entry.target.id
						}
						setActiveId(entry.target.id)
					}
				})
			},
			{ rootMargin: `0% 0% -80% 0%` }
		)
		itemIds.forEach(id => {
			if (document.getElementById(id) !== null) {
				observer.observe(document.getElementById(id))
			}
		})
		return () => {
			itemIds.forEach(id => {
				if (document.getElementById(id) !== null) {
					observer.unobserve(document.getElementById(id))
				}
			})
		}
	}, [itemIds])
	return activeId
}

function renderItems(items, activeId) {
	const pageValues = useContext(AppContext)
	if (activeId === null && pageValues.activeTOC.current !== null) {
		activeId = pageValues.activeTOC.current
	}
	return (
		<ol>
			{items.map(item => {
				const classNames = classnames(
					'block w-full rounded-md outline-none text-sm px-2 py-1 font-semibold hover:text-gray-900 hover:bg-gray-200 focus:bg-gray-200 focus:text-gray-200 transition duration-150 ease-in-out break-all cursor-pointer',
					activeId === item.url.slice(1)
						? 'bg-gray-100 text-gray-900'
						: 'text-gray-700 dark:text-gray-200 dark:hover:text-gray-900'
				)
				return (
					<li key={item.url}>
						<Link
							to={item.url.slice(1)}
							smooth={true}
							offset={-128}
							duration={800}
							className={classNames}
							onClick={() => pageValues.setIsOpenTOC(false)}
						>
							{item.title}
						</Link>
						{item.items && renderItems(item.items, activeId)}
					</li>
				)
			})}
		</ol>
	)
}

function TableOfContents(props) {
	const { items } = props
	const idList = getIds(items)
	const activeId = useActiveId(idList)
	return <div className="table-of-contents">{renderItems(items, activeId)}</div>
}

TableOfContents.propTypes = {
	items: PropTypes.array,
}

export default TableOfContents
