import React, { useContext } from 'react'

import { AppContext } from '@src-components/contexts'

import LightLogo from '@static/logo/light.svg'
import DarkLogo from '@static/logo/dark.svg'

const Logo = props => {
	const pageValues = useContext(AppContext)

	return pageValues.isDarkMode ? (
		<DarkLogo {...props} />
	) : (
		<LightLogo {...props} />
	)
}

export default Logo
