import React from 'react'
import { css } from '@emotion/core'

import { AvatarImage } from '@src-components/images'

import { BlockOptions } from '@src-components/mdx'
import Markdown from 'markdown-to-jsx'

const Balloon = props => {
	const { children } = props
	const imagePath = `chara-mini/gorilla.png`

	const resultChildren = []
	if (typeof children === 'object') {
		children.map(data => {
			if (typeof data !== 'object') {
				resultChildren.push(
					<Markdown
						options={{
							forceInline: false,
							overrides: BlockOptions,
						}}
					>
						{data}
					</Markdown>
				)
			} else {
				resultChildren.push(data)
			}
		})
	} else {
		resultChildren.push(
			<Markdown
				options={{
					forceInline: false,
					overrides: BlockOptions,
				}}
			>
				{children}
			</Markdown>
		)
	}

	return (
		<div
			className="my-4 flex items-end sm:items-start flex-col sm:flex-row"
			css={css`
				&::after {
					clear: both;
					content: '';
					display: table;
				}
			`}
		>
			<div className="inline-flex items-end m-0 order-1 justify-center">
				<AvatarImage
					filePath={imagePath}
					alt="逆引きゴリラ"
					className="w-32 h-36 relative"
				/>
			</div>
			<div
				className="flex-auto p-4 relative bg-gray-100 dark:bg-gray-700 rounded-md w-full sm:mr-2 text-base"
				css={css`
					&::after {
						border-bottom-color: inherit;
						border-bottom-style: solid;
						border-left: 5px solid transparent;
						border-right: 5px solid transparent;
						border-top: 16px solid #f4f5f7;
						border-width: 16px 0 0 16px;
						bottom: -16px;
						content: '';
						display: block;
						height: 0;
						left: auto;
						margin-left: 0;
						position: absolute;
						right: 128px;
						top: auto;
						width: 0;
					}
					.dark &::after {
						--border-opacity: 1;
						border-top: 16px solid rgba(55, 65, 81, var(--border-opacity));
					}
					@media (min-width: 640px) {
						&::after {
							border-color: transparent transparent transparent #f4f5f7;
							border-width: 16px 0 0 16px;
							margin-top: 0;
							right: -16px;
							top: 32px;
						}
						.dark &::after {
							--border-opacity: 1;
							border-color: transparent transparent transparent
								rgba(55, 65, 81, var(--border-opacity));
						}
					}
				`}
			>
				{resultChildren}
			</div>
		</div>
	)
}

export default Balloon
