import React from 'react'

import { Wrapper } from '@src-components/elements'
import { Copyright } from '@src-components/parts'
import { widgetSetting } from '@src-settings/widget.setting'

const Footer = () => {
	return (
		<footer className="relative py-4">
			<Wrapper>
				<div className="flex flex-wrap">
					{widgetSetting.footer.map((item, index) => {
						let Widget = item.kind
						return (
							<div
								key={index}
								className="w-full md:w-1/3 pr-0 md:pr-4 last:pr-0 mb-8 md:mb-0 last:mb-0"
							>
								<Widget label={item.label} icon={item.icon} args={item.args} />
							</div>
						)
					})}
				</div>
				<Copyright />
			</Wrapper>
		</footer>
	)
}

export default Footer
