import React from 'react'
import PropTypes from 'prop-types'
import GatsbyImage from 'gatsby-image'

import { AllAvatarImageQuery } from '@src-queries'

const AvatarImage = props => {
	const { filePath, alt, className } = props

	const allAvatarImage = AllAvatarImageQuery()

	const imageData = allAvatarImage.find(avatarImage => {
		return avatarImage.node.relativePath.includes(filePath)
	})
	if (!imageData) return null

	return (
		<GatsbyImage
			fluid={imageData.node.childImageSharp.fluid}
			alt={alt}
			className={className}
		/>
	)
}

AvatarImage.propTypes = {
	filePath: PropTypes.string.isRequired,
	alt: PropTypes.string,
	className: PropTypes.string,
}

export default AvatarImage
