import { useStaticQuery, graphql } from 'gatsby'

export const AllTagContentQuery = () => {
	const { allMdx } = useStaticQuery(
		graphql`
			query {
				allMdx(
					sort: { fields: [frontmatter___order], order: ASC }
					filter: {
						frontmatter: { draft: { ne: true } }
						fields: { type: { eq: "tag" } }
					}
				) {
					edges {
						node {
							id
							body
							frontmatter {
								title
								description
								thumbnail
								order
							}
							fields {
								slug
								name
							}
						}
					}
				}
			}
		`
	)
	return allMdx.edges
}
