import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'

import { WidgetWrapper } from '@src-components/widgets'

const LinkWidget = props => {
	const { label, icon, args } = props
	return (
		<WidgetWrapper label={label} icon={icon}>
			<nav>
				<ul>
					{args.items.map(item => {
						return (
							<li key={item.label} className="leading-6">
								<Link
									to={item.url}
									className="font-semibold text-sm text-gray-600 hover:text-gray-900
									dark:text-gray-200 dark:hover:text-gray-100
									"
								>
									{item.label}
								</Link>
							</li>
						)
					})}
				</ul>
			</nav>
		</WidgetWrapper>
	)
}

LinkWidget.propTypes = {
	label: PropTypes.string.isRequired,
	icon: PropTypes.object,
	args: PropTypes.object,
}

export default LinkWidget
