import React from 'react'
import Moment from 'react-moment'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'

import { faHistory } from '@fortawesome/free-solid-svg-icons'
import { faClock, faNewspaper } from '@fortawesome/free-regular-svg-icons'

import { FAIcon } from '@src-components/elements'
import { ThumbnailImage } from '@src-components/images'
import { SectionHeading } from '@src-components/parts'

const RelatedPosts = props => {
	const { items } = props
	return (
		<section className="border-gray-900 border-t-2 py-4 dark:border-gray-100">
			<SectionHeading icon={faNewspaper}>関連記事</SectionHeading>
			<div>
				{items.length > 0 ? (
					<ul className="grid grid-cols-1 gap-y-8 lg:grid-cols-2 xl:grid-cols-3 lg:gap-x-5 lg:gap-y-6">
						{items.map(item => {
							return (
								<li key={item.slug}>
									<Link to={item.slug} className="block group">
										<figure>
											<div className="relative rounded-sm overflow-hidden border-gray-200 border">
												<ThumbnailImage
													filePath={item.thumbnail}
													alt={item.title}
													className="w-full h-auto"
												/>
											</div>
											<figcaption className="mt-1 bg-gray-900 dark:bg-gray-700 text-white px-2 py-1 rounded-sm group-hover:bg-gray-700 dark:group-hover:bg-gray-500 shadow-lg">
												<p className="text-base font-black">{item.title}</p>
												<p className="text-sm font-bold">{item.description}</p>
												<div>
													{item.modifiedTime && (
														<div className="inline-block ml-4 first:ml-0">
															<p className="text-sm flex items-center">
																<FAIcon
																	icon={faHistory}
																	size="sm"
																	className="mr-1"
																/>
																<Moment
																	date={item.modifiedTime}
																	format="YYYY年MM月DD日"
																	className="inline-block"
																/>
															</p>
														</div>
													)}
													<div className="inline-block ml-4 first:ml-0">
														<p className="text-sm flex items-center">
															<FAIcon
																icon={faClock}
																size="sm"
																className="mr-1"
															/>
															<Moment
																date={item.publishTime}
																format="YYYY年MM月DD日"
																className="inline-block"
															/>
														</p>
													</div>
												</div>
											</figcaption>
										</figure>
									</Link>
								</li>
							)
						})}
					</ul>
				) : (
					<p>関連記事はありません。</p>
				)}
			</div>
		</section>
	)
}

RelatedPosts.propTypes = {
	items: PropTypes.array,
}

export default RelatedPosts
