import React from 'react'
import { Link } from 'gatsby'
import classnames from 'classnames'

import { FAIcon, Badge } from '@src-components/elements'
import { navigationSetting } from '@src-settings/navigation.setting'

const DrawerNav = () => {
	return (
		<>
			<h3 className="block font-bold text-sm text-white bg-gray-900 px-4 dark:text-gray-900 dark:bg-white">
				コンテンツ
			</h3>
			<nav>
				<ul>
					{navigationSetting.drawerNavItems.map(item => {
						return (
							<li
								key={item.url}
								className="border-gray-200 border-b outline-none dark:border-gray-700"
							>
								<Link
									to={item.url}
									className={classnames(
										'w-full relative flex items-center p-2 hover:bg-gray-100 focus:bg-gray-100 transition duration-150 ease-in-out font-bold text-base dark:hover:text-gray-900 dark:focus:text-gray-900',
										item.url === location.pathname
											? 'border-gray-900 border-l-2 dark:border-gray-200'
											: null
									)}
								>
									<FAIcon icon={item.icon} size="lg" className="mr-3" />
									{item.label}
									{item.badge && (
										<Badge textColor="text-white" bgColor="bg-gray-900">
											{item.badge}
										</Badge>
									)}
								</Link>
							</li>
						)
					})}
				</ul>
			</nav>
		</>
	)
}

export default DrawerNav
