import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'

import { faTags } from '@fortawesome/free-solid-svg-icons'

import { FAIcon, ScreenReaderText } from '@src-components/elements'
import { AllTagContentQuery } from '@src-queries'

const MetaItemTags = props => {
	const { items } = props
	if (!items) return null

	const allTagTypeData = AllTagContentQuery()
	return (
		<div className="flex items-center justify-items-center mb-2 last:mb-0 text-gray-600 text-sm leading-none dark:text-gray-200">
			<dt>
				<FAIcon icon={faTags} />
				<ScreenReaderText>タグ</ScreenReaderText>
			</dt>
			<dd>
				{items.map(name => {
					const tagItemData = allTagTypeData.find(
						data => data.node.fields.name === name
					)
					if (!tagItemData) return null

					return (
						<Link
							key={name}
							to={tagItemData.node.fields.slug}
							className="inline-block text-sm border border-gray-600 rounded-sm px-1 ml-1 dark:border-gray-200"
						>
							{tagItemData.node.frontmatter.title}
						</Link>
					)
				})}
			</dd>
		</div>
	)
}

MetaItemTags.propTypes = {
	items: PropTypes.array,
}

export default MetaItemTags
