/**
 * Layout
 */
import React from 'react'
import PropTypes from 'prop-types'

import { OneColumnLayout } from '@src-layouts/one-column'
import { SimpleLayout } from '@src-layouts/simple'
import { TwoColumnLayout } from '@src-layouts/two-column'

const Layout = props => {
	const { pattern, children } = props

	switch (pattern) {
		case 'one-column':
			return <OneColumnLayout>{children}</OneColumnLayout>
		case 'simple':
			return <SimpleLayout>{children}</SimpleLayout>
		case 'two-column':
			return <TwoColumnLayout>{children}</TwoColumnLayout>
		default:
			return <TwoColumnLayout>{children}</TwoColumnLayout>
	}
}

Layout.propTypes = {
	pattern: PropTypes.string,
	children: PropTypes.node.isRequired,
}

export default Layout
