import React from 'react'
import Moment from 'react-moment'
import { Link } from 'gatsby'

import { faCubes, faHistory } from '@fortawesome/free-solid-svg-icons'
import { faClock } from '@fortawesome/free-regular-svg-icons'

import { FAIcon, ScreenReaderText } from '@src-components/elements'
import { ThumbnailImage } from '@src-components/images'
import { AllCategoryContentQuery } from '@src-queries'

const ArticleListItem = props => {
	const { item } = props

	const allCategoryTypeData = AllCategoryContentQuery()

	return (
		<li>
			<article className="break-all">
				<Link to={item.node.fields.slug} className="block group">
					<figure>
						<div className="relative rounded-sm overflow-hidden border-gray-200 dark:border-gray-700 border shadow-sm">
							<ThumbnailImage
								filePath={item.node.frontmatter.thumbnail}
								alt={item.node.frontmatter.title}
								className="w-full h-auto"
							/>
						</div>
						<figcaption
							className="
								mt-1 bg-white border-gray-100 border px-2 py-1 rounded-sm group-hover:bg-gray-100 shadow
								dark:bg-gray-700 dark:group-hover:bg-gray-500 dark:border-gray-700
							"
						>
							<p className="font-bold text-lg">{item.node.frontmatter.title}</p>
							<p className="font-semibold text-sm ">
								{item.node.frontmatter.description}
							</p>
							<div>
								{item.node.frontmatter.modifiedTime && (
									<div className="inline-block ml-4 first:ml-0">
										<p className="text-sm  flex items-center">
											<FAIcon icon={faHistory} size="sm" className="mr-1" />
											<Moment
												date={item.node.frontmatter.modifiedTime}
												format="YYYY年MM月DD日"
												className="inline-block"
											/>
										</p>
									</div>
								)}
								<div className="inline-block ml-4 first:ml-0">
									<p className="text-sm flex items-center">
										<FAIcon icon={faClock} size="sm" className="mr-1" />
										<Moment
											date={item.node.frontmatter.publishTime}
											format="YYYY年MM月DD日"
											className="inline-block"
										/>
									</p>
								</div>
							</div>
							<div className="flex items-start text-sm ">
								<dt>
									<FAIcon icon={faCubes} size="sm" />
									<ScreenReaderText>カテゴリー</ScreenReaderText>
								</dt>
								<dd>
									{item.node.frontmatter.categories.map(name => {
										const categoryItemData = allCategoryTypeData.find(
											data => data.node.fields.name === name
										)
										if (!categoryItemData) return null
										return (
											<span
												key={categoryItemData.node.id}
												className="inline-block border border-gray-900 dark:border-gray-100 rounded-sm px-1 ml-1"
											>
												{categoryItemData.node.frontmatter.title}
											</span>
										)
									})}
								</dd>
							</div>
						</figcaption>
					</figure>
				</Link>
			</article>
		</li>
	)
}

export default ArticleListItem
