import React from 'react'
import PropTypes from 'prop-types'
import { Link } from 'gatsby'

import { faCubes } from '@fortawesome/free-solid-svg-icons'

import { FAIcon, ScreenReaderText } from '@src-components/elements'
import { AllCategoryContentQuery } from '@src-queries'

const MetaItemCategories = props => {
	const { items } = props
	if (!items) return null

	const allCategoryTypeData = AllCategoryContentQuery()

	return (
		<div className="flex items-center justify-items-center mb-2 last:mb-0 text-gray-600 text-sm leading-none dark:text-gray-200">
			<dt>
				<FAIcon icon={faCubes} />
				<ScreenReaderText>カテゴリー</ScreenReaderText>
			</dt>
			<dd>
				{items.map(name => {
					const categoryItemData = allCategoryTypeData.find(
						data => data.node.fields.name === name
					)
					if (!categoryItemData) return null

					return (
						<Link
							key={name}
							to={categoryItemData.node.fields.slug}
							className="inline-block text-sm border border-gray-600 rounded-sm px-1 ml-1 dark:border-gray-200"
						>
							{categoryItemData.node.frontmatter.title}
						</Link>
					)
				})}
			</dd>
		</div>
	)
}

MetaItemCategories.propTypes = {
	items: PropTypes.array,
}

export default MetaItemCategories
