import { useStaticQuery, graphql } from 'gatsby'

export const AllSiteMetadataQuery = () => {
	const { site } = useStaticQuery(
		graphql`
			query {
				site {
					siteMetadata {
						title
						description
						siteUrl
						pathPrefix
						lang
						locale
						copyright
					}
				}
			}
		`
	)
	return site.siteMetadata
}
