import { useStaticQuery, graphql } from 'gatsby'

export const AllMediaImageQuery = () => {
	const { allFile } = useStaticQuery(
		graphql`
			query {
				allFile(
					filter: {
						sourceInstanceName: { eq: "contents-images-media" }
						ext: { regex: "/(png|jpg|jpeg)/" }
					}
				) {
					edges {
						node {
							relativePath
							sourceInstanceName
							childImageSharp {
								fluid(maxWidth: 1280, quality: 100) {
									...GatsbyImageSharpFluid
								}
							}
						}
					}
				}
			}
		`
	)

	return allFile.edges
}
