import React from 'react'
import PropTypes from 'prop-types'

import { css } from '@emotion/core'
import { faSearch } from '@fortawesome/free-solid-svg-icons'

import { FAIcon } from '@src-components/elements'

const SearchForm = props => {
	const { value, onFocus, onBlur, onChange } = props
	return (
		<div
			className="border shadow-sm border-gray-300 text-gray-900 rounded-sm relative mx-auto px-2
			dark:border-gray-700 dark:text-gray-100
			"
		>
			<FAIcon size="sm" icon={faSearch} />
			<input
				type="search"
				name="search"
				className="h-8 pl-2 focus:outline-none bg-transparent"
				css={css`
					width: calc(100% - 1.25rem);
					&::placeholder {
						color: rgba(210, 214, 220, 1);
						font-size: 0.75em;
					}
				`}
				placeholder="キーワードを入力"
				value={value}
				onFocus={onFocus}
				onBlur={onBlur}
				onChange={onChange}
			/>
		</div>
	)
}

SearchForm.propTypes = {
	value: PropTypes.string,
	onFocus: PropTypes.func,
	onBlur: PropTypes.func,
	onChange: PropTypes.func,
}

export default SearchForm
