import React, { useContext } from 'react'
import { css } from '@emotion/core'
import { Link } from 'gatsby'

import { AppContext } from '@src-components/contexts'
import { FAIcon, Wrapper } from '@src-components/elements'

const BreadcrumbList = () => {
	const pageValues = useContext(AppContext)

	const breadcrumbListItems = pageValues.breadcrumbItems.map(
		breadcrumbListItem => {
			return (
				<li
					className="inline-flex items-center whitespace-nowrap"
					css={css`
						&:not(:first-of-type):before {
							--bg-opacity: 1;
							border-color: rgba(107, 114, 128, var(--bg-opacity));
							border-style: solid;
							border-width: 2px 2px 0 0;
							content: '';
							display: inline-block;
							height: 0.5em;
							margin: 0em 0.75em;
							transform: rotate(45deg);
							width: 0.5em;
						}
						.dark &:not(:first-of-type):before {
							--bg-opacity: 1;
							border-color: rgba(229, 231, 235, var(--bg-opacity));
						}
					`}
					key={breadcrumbListItem.url}
				>
					<Link
						to={breadcrumbListItem.url}
						className="flex items-center justify-items-center whitespace-nowrap hover:text-gray-900 text-sm leading-none font-semibold
						dark:hover:text-gray-300
						"
					>
						<FAIcon icon={breadcrumbListItem.icon} size="sm" className="mr-1" />
						{breadcrumbListItem.label}
					</Link>
				</li>
			)
		}
	)

	return (
		<Wrapper>
			<ol className="flex items-center overflow-x-auto text-gray-700 text-sm font-medium py-2 dark:text-gray-100">
				{breadcrumbListItems}
			</ol>
		</Wrapper>
	)
}

export default BreadcrumbList
