import React from 'react'
import PropTypes from 'prop-types'
import { Transition } from '@headlessui/react'

const SlideContents = props => {
	const { isOpen, children } = props
	return (
		<Transition show={isOpen}>
			<div className="fixed inset-0 z-50">
				<div className="relative w-full h-full flex">
					<section className="absolute right-0 inset-y-0 w-screen sm:w-1/2 md:w-1/3 xl:w-1/4">
						<Transition.Child
							enter="transform transition ease-in-out duration-300"
							enterFrom="translate-x-full"
							enterTo="translate-x-0"
							leave="transform transition ease-in-out duration-300"
							leaveFrom="translate-x-0"
							leaveTo="translate-x-full"
						>
							<div className="flex flex-col flex-1 bg-white dark:bg-gray-900 shadow-xl h-screen border-l dark:border-gray-700">
								{children}
							</div>
						</Transition.Child>
					</section>
				</div>
			</div>
		</Transition>
	)
}

SlideContents.propTypes = {
	isOpen: PropTypes.bool.isRequired,
	children: PropTypes.node,
}

export default SlideContents
