import React from 'react'
import PropTypes from 'prop-types'

const SpiderCarousel = props => {
	const { children } = props

	const canvas = []
	const dots = []
	children.map((item, index) => {
		canvas.push(
			<div key={index} className="spider__slide" data-id={index}>
				<div className="spider__figure">{item}</div>
			</div>
		)
		dots.push(
			<button key={index} className="spider__dot" data-id={index}>
				{index + 1}
			</button>
		)
	})

	return (
		<div className="spider-carousel mb-6" data-fade="true" data-interval="5000">
			<div className="spider">
				<div className="spider__canvas">{canvas}</div>
			</div>
			<div className="spider__dots">{dots}</div>
		</div>
	)
}

SpiderCarousel.propTypes = {
	children: PropTypes.node.isRequired,
}

export default SpiderCarousel
