import React, { useContext, useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import { css } from '@emotion/core'

import { faBook, faSearch, faBars } from '@fortawesome/free-solid-svg-icons'

import { AppContext } from '@src-components/contexts'
import { Header, Footer } from '@src-components/foundations'
import { WidgetWrapper } from '@src-components/widgets'
import {
	News,
	BreadcrumbList,
	TableOfContents,
	SlideContents,
	SlideContentsHeader,
	SlideContentsContent,
	SearchForm,
	SearchResult,
	DrawerNav,
} from '@src-components/parts'

export const TwoColumnLayout = props => {
	const { children } = props

	const pageValues = useContext(AppContext)

	useEffect(() => {
		pageValues?.isOpenMenu || pageValues?.isOpenTOC || pageValues?.isOpenSearch
			? document.body.classList.add('overflow-hidden')
			: document.body.classList.remove('overflow-hidden')
	}, [pageValues?.isOpenMenu, pageValues?.isOpenTOC, pageValues?.isOpenSearch])

	useEffect(() => {
		if (pageValues?.isOpenTOC) {
			const tocElements = document.querySelectorAll('.table-of-contents')
			for (const tocElement of tocElements) {
				if (tocElement.dataset.active !== null) {
					pageValues.activeTOC.current = tocElement.dataset.active
					return
				}
			}
		}
	}, [pageValues?.isOpenTOC])

	const [searchValue, setSearchValue] = useState('')
	const [searchResultData, setSearchResultData] = useState([])

	return (
		<div className="bg-white dark:bg-gray-900 text-gray-900 dark:text-gray-100 flex flex-col min-h-screen tracking-wide leading-8">
			<Header />
			<News />
			<BreadcrumbList />
			<main className="flex-grow pt-8 pb-12">
				<div className="container mx-auto px-4 sm:flex sm:flex-row sm:flex-nowrap">
					<article className="w-full md:w-2/3 lg:w-3/4 xl:w-4/5 break-all">
						{children}
					</article>
					<aside className="hidden md:block md:w-1/3 lg:w-1/4 xl:w-1/5 relative">
						<div className="sticky top-36 z-10 bg-white ml-8 dark:bg-gray-900">
							{pageValues?.tocItems && (
								<WidgetWrapper label="目次" icon={faBook}>
									<div
										className="overflow-y-auto"
										css={css`
											height: calc(100vh - 256px);
										`}
									>
										<TableOfContents items={pageValues?.tocItems} />
									</div>
								</WidgetWrapper>
							)}
						</div>
					</aside>
				</div>
			</main>
			<Footer />
			<SlideContents isOpen={pageValues?.isOpenMenu}>
				<SlideContentsHeader
					title="メニュー"
					icon={faBars}
					close={pageValues?.setIsOpenMenu}
				></SlideContentsHeader>
				<SlideContentsContent>
					<DrawerNav />
				</SlideContentsContent>
			</SlideContents>
			<SlideContents isOpen={pageValues?.isOpenSearch}>
				<SlideContentsHeader
					title="記事検索"
					icon={faSearch}
					close={pageValues?.setIsOpenSearch}
				>
					<SearchForm
						value={searchValue}
						onChange={event => setSearchValue(event.target.value)}
					/>
				</SlideContentsHeader>
				<SlideContentsContent>
					<SearchResult
						value={searchValue}
						searchResultData={searchResultData}
						setSearchResultData={setSearchResultData}
					/>
				</SlideContentsContent>
			</SlideContents>
			<SlideContents isOpen={pageValues?.isOpenTOC}>
				<SlideContentsHeader
					title="目次"
					icon={faBook}
					close={pageValues?.setIsOpenTOC}
				></SlideContentsHeader>
				<SlideContentsContent>
					<div className="px-2">
						<TableOfContents items={pageValues?.tocItems} />
					</div>
				</SlideContentsContent>
			</SlideContents>
		</div>
	)
}

TwoColumnLayout.propTypes = {
	children: PropTypes.node.isRequired,
}
