import React from 'react'
import PropTypes from 'prop-types'
import Moment from 'react-moment'

import { faHistory } from '@fortawesome/free-solid-svg-icons'

import { FAIcon, ScreenReaderText } from '@src-components/elements'

const MetaItemModifiedTime = props => {
	const { item } = props
	if (!item) return null

	return (
		<div className="inline-block mb-2 last:mb-0 ml-4 first:ml-0">
			<div className="flex items-center justify-items-center text-gray-600 text-sm leading-none dark:text-gray-200">
				<dt>
					<FAIcon icon={faHistory} />
					<ScreenReaderText>更新日時</ScreenReaderText>
				</dt>
				<dd>
					<Moment
						date={item}
						format="YYYY年MM月DD日 hh:mm"
						className="inline-block text-sm ml-1"
					/>
				</dd>
			</div>
		</div>
	)
}

MetaItemModifiedTime.propTypes = {
	item: PropTypes.string,
}

export default MetaItemModifiedTime
