import React from 'react'

import { useIntl, Link } from 'gatsby-plugin-intl'

import { faBullhorn } from '@fortawesome/free-solid-svg-icons'

import { FAIcon, Wrapper } from '@src-components/elements'
import { newsSetting } from '@src-settings/news.setting'

const News = () => {
	if (!newsSetting.message) return null

	const intl = useIntl()
	return (
		<div className="w-full border-b border-gray-200 dark:border-gray-700">
			<Wrapper>
				<div className="pt-2 pb-1 sm:flex sm:items-center sm:justify-items-center">
					<span className="inline-flex items-center justify-items-center p-1 text-gray-900 text-sm leading-none font-semibold border-gray-900 border rounded sm:mr-2 whitespace-nowrap shadow dark:text-gray-100 dark:border-gray-100">
						<FAIcon icon={faBullhorn} size="sm" className="mr-1" />
						{intl.formatMessage({ id: 'news.label' })}
					</span>
					<div className="text-sm mt-1 sm:mt-0 font-medium">
						{newsSetting.url ? (
							<Link
								to={newsSetting.url}
								className="text-pink-600 dark:text-teal-300"
							>
								{newsSetting.message}
							</Link>
						) : (
							newsSetting.message
						)}
					</div>
				</div>
			</Wrapper>
		</div>
	)
}

export default News
