import React, { useState, useRef, useEffect } from 'react'
import PropTypes from 'prop-types'

import { useStaticQuery, graphql, Link } from 'gatsby'

const SearchResult = props => {
	const { value, searchResultData, setSearchResultData } = props

	const init = useRef(true)

	const allPostData = useStaticQuery(
		graphql`
			query {
				allMdx(
					sort: { fields: [frontmatter___modifiedTime], order: DESC }
					filter: {
						frontmatter: { draft: { ne: true } }
						fields: { type: { eq: "post" } }
					}
					limit: 100
				) {
					edges {
						node {
							id
							frontmatter {
								title
								description
								publishTime
								modifiedTime
								categories
								tags
								keywords
							}
							fields {
								slug
								name
								type
							}
						}
					}
				}
			}
		`
	)

	const [targetData, setTargetData] = useState([])
	const [displayState, setDisplayState] = useState('hidden')

	useEffect(() => {
		const _targetData = []
		allPostData.allMdx.edges.map(edge => {
			_targetData.push(edge.node)
		})
		setTargetData(_targetData)
	}, [])

	useEffect(() => {
		const _display = value.length ? 'block' : 'hidden'
		const _id = setTimeout(() => {
			setDisplayState(_display)
		}, 100)
		return () => {
			clearTimeout(_id)
		}
	}, [value])

	const [] = useState([])
	const search = () => {
		const _value = value.toLowerCase()
		const _temp = targetData.filter(node => {
			const _target = `
				${node.frontmatter.title.toLowerCase()}
				${node.frontmatter.description.toLowerCase()}
				${node.frontmatter.categories.join(' ').toLowerCase()}
				${node.frontmatter.tags.join(' ').toLowerCase()}
				${node.frontmatter.keywords.join(' ').toLowerCase()}
			`
			return _target.indexOf(_value) !== -1
		})
		setSearchResultData(_temp)
	}

	useEffect(() => {
		value.length && !init.current && search()
		init.current = false
	}, [value])

	return (
		<div className={displayState}>
			<div className="font-semibold text-sm px-2 mt-3 mb-2">
				{searchResultData.length
					? `該当する記事が${searchResultData.length}件あります`
					: '該当する記事はありません'}
			</div>
			<nav className="px-2">
				<ul>
					{searchResultData.map(result => {
						return (
							<li key={result.id} className="mb-2">
								<Link
									to={result.fields.slug}
									className="px-2 py-1 block rounded-sm transition ease-in-out duration-150
									bg-gray-50 text-gray-900 dark:bg-gray-800 dark:text-gray-200
									hover:bg-gray-900 hover:text-white dark:hover:bg-gray-700 dark:hover:text-gray-100
									focus:bg-gray-900 focus:text-white dark:focus:bg-gray-700 dark:focus:text-gray-100
									"
								>
									<dl>
										<dt className="text-sm font-bold">
											{result.frontmatter.title}
										</dt>
										<dd className="text-xs font-semibold">
											{result.frontmatter.description}
										</dd>
									</dl>
								</Link>
							</li>
						)
					})}
				</ul>
			</nav>
		</div>
	)
}

SearchResult.propTypes = {
	value: PropTypes.string,
	searchResultData: PropTypes.array,
	setSearchResultData: PropTypes.func,
}

export default SearchResult
