import React from 'react'
import PropTypes from 'prop-types'

import { BlockOptions } from '@src-components/mdx'
import Markdown from 'markdown-to-jsx'

import { FAIcon } from '@src-components/elements'
import { faQuestion } from '@fortawesome/free-solid-svg-icons'

const QAQuestion = props => {
	const { children } = props

	const resultChildren = []
	if (typeof children === 'object') {
		children.map(data => {
			if (typeof data !== 'object') {
				resultChildren.push(
					<Markdown
						options={{
							forceInline: false,
							overrides: BlockOptions,
						}}
					>
						{data}
					</Markdown>
				)
			} else {
				resultChildren.push(data)
			}
		})
	} else {
		resultChildren.push(
			<Markdown
				options={{
					forceInline: false,
					overrides: BlockOptions,
				}}
			>
				{children}
			</Markdown>
		)
	}

	return (
		<div className="flex flex-nowrap flex-row items-start mb-4">
			<div
				className="w-8 h-8 flex justify-center items-center rounded-sm border-2
			bg-gray-100 border-gray-700 text-gray-900
			dark:bg-gray-700 dark:border-gray-100 dark:text-white
			"
			>
				<FAIcon icon={faQuestion} size="sm" />
			</div>
			<div className="flex-auto ml-4 font-extrabold text-xl">
				{resultChildren}
			</div>
		</div>
	)
}

QAQuestion.propTypes = {
	children: PropTypes.node.isRequired,
}

export default QAQuestion
