import { useStaticQuery, graphql } from 'gatsby'

export const AllCategoriesGroupQuery = () => {
	const { allMdx } = useStaticQuery(
		graphql`
			{
				allMdx(
					filter: {
						frontmatter: { draft: { ne: true } }
						fields: { type: { eq: "post" } }
					}
				) {
					group(field: frontmatter___categories) {
						fieldValue
						totalCount
					}
				}
			}
		`
	)
	return allMdx.group
}
