import React from 'react'
import PropTypes from 'prop-types'
import Moment from 'react-moment'

import { faClock } from '@fortawesome/free-regular-svg-icons'

import { FAIcon, ScreenReaderText } from '@src-components/elements'

const MetaItemPublishTime = props => {
	const { item } = props
	if (!item) return null

	return (
		<div className="inline-block mb-2 last:mb-0 ml-4 first:ml-0">
			<div className="flex items-center justify-items-center text-gray-600 text-sm leading-none dark:text-gray-200">
				<dt>
					<FAIcon icon={faClock} />
					<ScreenReaderText>公開日時</ScreenReaderText>
				</dt>
				<dd>
					<Moment
						date={item}
						format="YYYY年MM月DD日 hh:mm"
						className="inline-block ml-1 text-sm"
					/>
				</dd>
			</div>
		</div>
	)
}

MetaItemPublishTime.propTypes = {
	item: PropTypes.string,
}

export default MetaItemPublishTime
